<template>
    <router-link @click="newCategory(category.id)" :to="{name: 'collections'}" class="header__menu-item-link">
      <li class="header__menu-item">
        <img :src="category.image" :alt="category.title" class="header__menu-item-img">
        <div class="header__menu-item-heading">{{ category.title }}</div>
      </li>
    </router-link>
</template>

<script>
import { mapMutations } from 'vuex';
export default {
    props: ['category'],
    methods: {
        ...mapMutations({
            changeCategory: 'changeCategory',
        }),
        newCategory(id) {
            this.changeCategory(id);
        }
    }
}
</script>

