<template>
  <li class="header__cart-item">
    <a href="#" class="header__cart-item-link"><img :src="product.product.images[0]" :alt="product.product.title" class="header__cart-item-img"></a>
    <div class="header__cart-item-about">
        <div class="header__cart-about-description">
            <div class="header__cart-about-text">
                <div class="header__cart-about-title">{{ product.product.title }}</div>
                <div class="header__cart-about-item">{{ product.product.description }}</div>
            </div>
            <div class="header__cart-about-price">${{ product.product.price }}</div>
        </div>
        <div class="header__cart-about-change">
            <div class="header__cart-change-input">
                <button @click.prevent="removeProductAmount(product.product.id)" class="header__cart-change-btn change-cart-item">-</button>
                <div class="header__cart-change-amount change-cart-item">{{ product.amount }}</div>
                <button @click.prevent="addProductAmount(product.product.id)" class="header__cart-change-btn change-cart-item">+</button>
            </div>
            <button @click.prevent="removeItem(product.product.id)" class="header__cart-about-change-remove">Remove</button>
        </div>
    </div>
  </li>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
    props: ['product'],
    methods: {
        ...mapMutations({
            addAmount: 'addAmount',
            removeAmount: 'removeAmount',
            removeProduct: 'removeProduct',
         }),
        addProductAmount(id) {
         this.addAmount(id);
        },
        removeProductAmount(id) {
         if (this.product.amount === 1) {return}
         this.removeAmount(id);
        },
        removeItem(id) {
            this.removeProduct(id);
        }
    },
}
</script>

<style scoped>
    .header__cart-item {
        display: flex;
    }
    
    .header__cart-item:not(:last-child) {
        margin-bottom: 20px;
    }

    .header__cart-item-img {
        max-width: 104px;
    }

    .header__cart-item-about {
