<template>
    <div class="checkout__form-wrapper-mini checkout__form-wrapper">
        <label :for="name" :class="{'label-active': value.length > 0}" class="label-helper">{{ labelName }}</label>
        <input required @input="updateInput" :class="{'input-active': value.length > 0}" :id="name" class="checkout__form-input-mini checkout__form-input input" :type="'type'" :placeholder="labelName">
    </div>
 </template>
 
 <script>
    export default {
        props: ['name', 'type', 'labelName', 'modelValue'],
        data() {
            return {
                value: '',
            }
        },
        methods: {
            updateInput(e) {
                this.value = e.target.value;
                this.$emit('update:modelValue', e.target.value)
            }
        }
    }
</script>
 
 <style scoped>
    .checkout__form-wrapper-mini {
        width: 100%;
        max-width: 210px;
    }
    .checkout__form-input-mini {
        max-width: 205px;
    }

    @media(max-width: 700px) {
        .checkout__form-input-mini {
            max-width: 29.29vw;
        }
        .checkout__form-wrapper-mini {
            max-width: 29.29vw;
        }
    }

    @media (max-width: 440px) {
        .checkout__form-wrapper-mini {
            max-width: 28.41vw;
        }
        .checkout__form-input-mini {
            max-width: 28.41vw;
        }
    }
 </style>