<script>
   import { mapMutations } from 'vuex'
   import categories from '@/data/categories'
   import HeaderMenuItem from '@/components/HeaderMenuItem.vue'

   export default {
        data() {
            return {
                categories,
            }
        },
        components: { HeaderMenuItem, },
        emits: ['update:menuOpen'],
        props: ['menuOpen'],
        methods: {
            ...mapMutations({
                changeCategory: 'changeCategory',
            }),
            closeMenu() {
                this.$emit('update:menuOpen', false);
            },
            newCategory(id) {
                this.changeCategory(id);
            }
        },
   }
