<template>
  <main-header/>
  <main>
    <section class="main-product">
        <div class="section-product-image">
            <div class="product-image__container">
                <img v-for="image in product.galeryImages" :key="image" :src="image" class="product-image__image">
            </div>
        </div>
        <div class="product-info">
            <section class="section-product-info">
                <div class="section-product__container">
                    <div class="product-info__base">
                        <h3 class="heading">{{ product.title }}</h3>
                        <div class="product-info__price">${{ product.price }}</div>
                    </div>
                    <product-about-options :comments="comments" :product="product"/>
                </div>
                <div class="container"></div>
            </section>
        </div>
    </section>
    <section class="section-comments">
        <div class="container">
            <h2 class="section-comments__heading heading">Reviews</h2>
            <base-comments v-for="comment in comments" :key="comment.id" :comment="comment" />
        </div>
    </section>
  </main>
  <main-footer/>
</template>

<script>
import axios from 'axios';
import products from '@/data/products';
import BaseComments from '@/components/BaseComments';
import ProductAboutOptions from '@/components/ProductAboutOptions';
import MainHeader from "@/components/MainHeader";
import MainFooter from "@/components/MainFooter";

export default {
    data() {
        return {
           comments: [],
        }
    },
    components: { ProductAboutOptions, BaseComments, MainHeader, MainFooter, },
    computed: {
        product() {
            return products.find(product => product.id === +this.$route.params.id)
        }
    },
    created() {
        axios.get(`https://jsonplaceholder.typicode.com/posts/${this.product.id}/comments`)
            .then(response => this.comments = response.data);
    }
}
</script>

