<template>
    <div class="checkout__form-wrapper">
        <label :for="name" :class="{'label-active': value.length > 0}" class="label-helper">{{ labelName }}</label>
        <input required @input="updateInput" :value="modelValue" :class="{'input-active': value.length > 0}" :id="name" class="checkout__form-input input" :type="type" :placeholder="labelName">
    </div>
</template>

<script>
export default {
    props: ['name', 'type', 'labelName', 'modelValue'],
    data() {
        return {
            value: '',
        }
    },
    methods: {
        updateInput(e) {
            this.value = e.target.value;
            this.$emit('update:modelValue', e.target.value)
        }
    }
}
</script>

