<template>
    <li class="header__cart-extra-item">
      <img :src="product.images[0]" alt="" class="header__cart-extra-img">
      <div class="header__cart-extra-about">
        <div class="header__cart-extra-heading">{{ product.title }}</div>
        <div class="header__cart-extra-description">${{ product.price }}</div>
        <button @click="addExtra(product.id)" class="header__cart-extra-button">add</button>
      </div>
    </li>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
  props: ['product'],
  methods: {
    ...mapMutations({
      addProduct: 'addProduct',
    }),
    addExtra(id) {
      this.addProduct({productId: id, size: this.product.sizes ? this.product.sizes[0].title : null, color: this.product.colors ? this.product.colors[0].title : null}); 
    },
  }
}
</script>

