<template>
  <form @submit="$router.push({name: 'order'}); clearCart()" method="post" class="checkout__form">
    <div class="checkout__form-contact">
        <div class="checkout__form-descriptions">
            <h3 for="email-input" class="checkout__label second-heading">Contact information</h3>
            <small class="checkout__account small-description">Already have an account? <a href="#" class="checkout__account-link">Log in</a></small>
        </div>
        <checkout-input v-model="formData.email" :type="'email'" :labelName="'Email'" :name="'email-input'"/> 
    </div>
    <checkout-checkbox :id="'email-news'" :text="'Email'" v-model="formData.news" />
    <div class="checkout__form-adress">
        <h3 for="email-input" class="checkout__address-heading second-heading">Contact information</h3>
        <checkout-select v-model="formData.country" :options="selectOptions" :label="'Country/region'" :name="'state'" />
        <div class="checkout__form-name">
            <checkout-input-normal :type="'text'" :name="'first-name'" :label-name="'First name (optional)'" v-model="formData.firstName"/>
            <checkout-input-normal :type="'text'" :name="'last-name'" :label-name="'Last name'" v-model="formData.lastName"/>
        </div>
        <checkout-input v-model="formData.address" :type="'text'" :labelName="'Address'" :name="'address'"/> 
        <checkout-input v-model="formData.apartment" :type="'text'" :labelName="'Apartment, suite, etc. (optional)'" :name="'apartment'"/> 
        <div class="checkout__form-apartments">
            <checkout-input-small :type="'text'" :name="'city'" :labelName="'City'" v-model="formData.city"/>
            <checkout-input-small :type="'text'" :name="'state'" :labelName="'State'" v-model="formData.state"/>
            <checkout-input-small :type="'number'" :name="'zip-code'" :labelName="'ZIP code'" v-model="formData.zip"/>
        </div>
        <checkout-input :type="'phone'" v-model="formData.phone" :labelName="'Phone'" :name="'phone-number'"/> 
    </div>
    <checkout-news v-model="formData.textNews" />
    <div class="checkout__form-buttons">
        <router-link :to="{name: 'collections'}" href="#" class="checkout__form-return">Return to catalog</router-link>
        <button type="submit" class="checkout__form-btn btn">Continue to shipping</button>
    </div>
  </form>
</template>

<script>
import { mapMutations } from 'vuex';
import CheckoutInput from '@/components/CheckoutInput';
import CheckoutInputNormal from '@/components/CheckoutInputNormal';
import CheckoutInputSmall from '@/components/CheckoutInputSmall';
import CheckoutSelect from '@/components/CheckoutSelect';
import CheckoutNews from '@/components/CheckoutNews';
import CheckoutCheckbox from '@/components/CheckoutCheckbox'

export default {
    data() {
        return {
            formData: {
                email: '',
                country: 'United States',
            },
            errors: false,
            selectOptions: [{name: 'United States', value: 'usa'},{name: 'Canada', value: 'canada'},{name: 'Australia', value: 'australia'},{name: 'United Kingdom', value: 'uk'},],
        };
    },
    components: {CheckoutInput, CheckoutSelect, CheckoutInputNormal, CheckoutInputSmall, CheckoutNews, CheckoutCheckbox},
    methods: {
        ...mapMutations ({
            removeProducts: 'removeProducts',
        }),
        clearCart() {
            this.removeProducts();
        },
    },
}
</script>

<style scoped>
    .checkout__form-descriptions {
        display: flex;
        justify-content: space-between;
    }
    .checkout__account-link {
        font-size: 14px;
        line-height: 16px;
    }
    .checkout__address-heading {
        margin-bottom: 25px;
    }
    .checkout__form-adress {
        margin-top: 40px;
    }
    .checkout__form-name {
        display: flex;
        justify-content: space-between;
    }
    .checkout__form-apartments {
        display: flex;
        justify-content: space-between;
    }
    .checkout__form-buttons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 35px 0px 45px 0px;
    }
    .checkout__form-btn {
        max-width: 220px;
        padding: 22px 0px;
    }
    .checkout__form-return {
        font-size: 16px;
        line-height: 19px;
    }
    .checkout__form-return::before {
        content: url(@/assets/img/svg/return-left.svg);
        margin-right: 10px;
        vertical-align: -2px;
    }

    @media (max-width: 700px) {
        .checkout__form-name {
            justify-content: space-between;
        }
    }

    @media (max-width: 440px) {
        .checkout__account-link {
            font-size: 3.18vw;
            line-height: 3.64vw;
        }
        .checkout__address-heading {
            margin-bottom: 5.68vw;
        }
        .checkout__form-adress {
            margin-top: 40px;
        }
        .checkout__form-wrapper {
            margin-top: 5.68vw;
        }
        .checkout__form-adress-select {
            padding-left: 2.27vw;
            padding-right: 2.27vw;
        }
        .checkout__form-buttons {
            margin: 7.95vw 0px 10.23vw 0px;
        }
        .checkout__form-btn {
            max-width: 50.00vw;
            padding: 5vw 0px;
        }
        .checkout__form-return {
            font-size: 3.64vw;
            line-height: 4.32vw;
        }
        .checkout__form-return::before {
            content: url(@/assets/img/svg/return-left_m.svg);
            margin-right: 2.27vw;
        }
    }
</style>