<script>
import { mapMutations } from 'vuex';

export default {
    data() {
        return {
            aboutOpen: false,
            color: this.product.colors ? this.product.colors[0] : false,
            size: this.product.sizes ? this.product.sizes[0] : false,
            sale: this.product.price / 100  * 75,
        }
    },
    props: ['product', 'comments'],
    methods: {
        ...mapMutations({
            addProduct: 'addProduct',
        }),
        openDescription() {
            this.aboutOpen = !this.aboutOpen;
        },
        addToCart() {
            this.addProduct({productId: this.product.id, size: this.size.title, color: this.color.title});
