<template>
    <form @submit.prevent="formSend" class="section-contact__form" action="#">
        <input required v-model="contactData.name" class="section-contact__input" type="text" placeholder="First and last name">
        <input required v-model="contactData.email" class="section-contact__input" type="email" placeholder="Email">
        <textarea v-model="contactData.comments" class="section-contact__input section-contact__input_text" type="text" placeholder="Message"/>
        <button  v-if="!messageSended" class="section-contact__btn btn">Submit</button>
        <div v-else-if="messageSended" class="section-contact__description section-contact__description_sended">You'r message sended!</div>
    </form>
</template>

<script>
export default {
    data() {
        return {
            contactData: {},
            messageSended: false,
        }
    },
    methods: {
        formSend() {
            this.contactData = {};
            this.messageSended = true;
        }
    }
}
</script>

