<template>
   <div class="checkout__form-wrapper checkout__form-wrapper-name">
        <label :for="name" :class="{'label-active': value.length > 0}" class="label-helper">{{ labelName }}</label>
        <input required @input="updateInput" :class="{'input-active': value.length > 0}" :id="name" class="checkout__form-input-name checkout__form-input input" :type="type" :placeholder="labelName">
    </div>
</template>

<script>
    export default {
        props: ['name', 'type', 'labelName', 'modelValue'],
        data() {
            return {
                value: '',
            }
        },
        methods: {
            updateInput(e) {
                this.value = e.target.value;
                this.$emit('update:modelValue', e.target.value)
            }
        }
    }
    </script>

<style scoped>
    .checkout__form-wrapper-name {
        max-width: 315px;
    }
    .checkout__form-input-name {
        width: 100%;
        max-width: 315px;
    }
    @media (max-width: 700px) {
        .checkout__form-input-name {
            max-width: 45vw;
        }
        .checkout__form-wrapper-name {
            max-width: 45vw;
        }
    }
    @media (max-width: 440px) { 
        .checkout__form-wrapper-name {
            max-width: 44.32vw;
        }
        .checkout__form-input-name {
            max-width: 44.32vw;
        }
    }
</style>