import { createStore } from 'vuex'
import products from '@/data/products'

let itemsArray = localStorage.getItem('items') ? JSON.parse(localStorage.getItem('items')) : [];

export default createStore({
  state: {
    cartProducts: localStorage.getItem('items') ? JSON.parse(localStorage.getItem('items')) : [],
    category: 0,
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        return  {
          product: products.find(product => product.id === item.productId),
          amount: item.amount,
          size: item.size,
          color: item.color,
        }
      });
    },
    cartTotalPrice(state, getters) {
      return {
        price() {
          if(state.cartProducts.length === 1) {
            return getters.cartDetailProducts[0].product.price * getters.cartDetailProducts[0].amount;
          } else {
            return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0)
          }
        },
        sale() {
          const tallage = 75;
          return (this.price() / 100 * tallage).toFixed(2);
        },
      }
    },
  },
  mutations: {
    addAmount(state, productId) {
      let item = state.cartProducts.find(product => product.productId === productId);
      let localItem = itemsArray.find(itemArray => itemArray.productId === productId);
      item.amount += 1;
      localItem.amount += 1;
      localStorage.setItem('items', JSON.stringify(itemsArray));
    },
    removeAmount(state, productId) {
      let item = state.cartProducts.find(product => product.productId === productId);
      let localItem = itemsArray.find(itemArray => itemArray.productId === productId);
      item.amount -= 1;
      localItem.amount -= 1;
      localStorage.setItem('items', JSON.stringify(itemsArray));
    },
    removeProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(product => product.productId !== productId);
      itemsArray = itemsArray.filter(itemArray => itemArray.productId !== productId);
      localStorage.setItem('items', JSON.stringify(itemsArray));
    },
    addProduct(state, product) {
      let { productId, size, color } = product;
      let item = state.cartProducts.find(product => product.productId === productId);
      if (item) {
        let localItem = itemsArray.find(itemArray => itemArray.productId === item.productId);
        localItem.amount += 1;
        item.amount += 1;
        localStorage.setItem('items', JSON.stringify(itemsArray));
      } else {
        state.cartProducts.push({productId: productId, amount: 1, size: size, color: color});
        itemsArray.push({productId: productId, amount: 1, size: size, color: color})
        localStorage.setItem('items', JSON.stringify(itemsArray));
      }
    },
    changeCategory(state, id) {
      state.category = id;
    },
    removeProducts(state) {
      state.cartProducts = [];
      itemsArray = [];
      localStorage.setItem('items', JSON.stringify(itemsArray));
    }
  },
})
