<template>
    <checkout-checkbox v-model="offer" :id="'offers'" :text="'Text'"/>
    <div v-show="offer" class="checkout__form-offer">
        <div class="checkout__form-wrapper">
            <span class="checkout__form-offer-icon"></span>
            <label for="number-news" :class="{'label-active': value.length > 0}" class="checkout__form-label_offer label-helper">Mobile phone number</label>
            <input @input="updateInput" :class="{'input-active': value.length > 0}" id="number-news" class="checkout__form-offer-input input" type="tel" placeholder="Mobile phone number">
        </div>
        <p class="checkout__form-offer-description small-description">By signing up via text, you agree to receive recurring automated marketing messages, including cart reminders, at the phone number provided. Consent is not a condition of purchase. Reply STOP to unsubscribe. Reply HELP for help. Message frequency varies. Msg & data rates may apply. View our <a href="#" class="checkout__form-offer-link">Privacy Policy</a> and <a href="#" class="checkout__form-offer-link">Terms of Service.</a></p>
    </div>
</template>

<script>
import CheckoutCheckbox from '@/components/CheckoutCheckbox';

export default {
    data() {
        return {
            value: '',
            offer: false,
        }
    },
    props: ['modelValue'],
    methods: {
        updateInput(e) {
            this.value = e.target.value;
            this.$emit('update:modelValue', e.target.value)
        }
    },
    components: {CheckoutCheckbox,}
}
</script>

