<template>
  <main-header/>
  <main class="main main-collections">
    <section class="section-catalog">
        <div class="container">
            <h2 class="section-catalog__heading heading">{{ categoryName.title }}</h2>
            <p class="section-catalog__description description">{{ categoryName.description }}</p>
        </div>
        <div class="container section-catalog__container">
            <product-list :products="products"/>
        </div>
    </section>
    <product-filter/>
  </main>
  <main-footer/>
</template>

<script>
import { mapState } from 'vuex';
import MainHeader from "@/components/MainHeader";
import MainFooter from "@/components/MainFooter";
import categories from '@/data/categories'
import products from '@/data/products'
import ProductList from '@/components/ProductList'
import ProductFilter from '@/components/ProductFilter'

export default {
    components: { ProductList, ProductFilter, MainHeader, MainFooter,},
    computed: {
        ...mapState({
            category: 'category',
        }),
        products() {
            if (this.category > 0) {
                return products.filter(product => product.categoryId === this.category);
            } else {
                return products;
            }
        },
        categoryName() {
            if (this.category > 0) {
                return categories.find(item => item.id === this.category)
            } else {
                return {title: 'Shop All'}
            }
        },
    },
}
</script>

<style scoped>

    .main-collections {
        display: flex;
        justify-content: space-between;
    }

    .section-catalog {
        padding-top: 40px;
        padding-bottom: 40px;
    }

    .section-catalog__container {
        max-width: 1300px;
        padding-left: 15px;
        padding-right: 15px;
        margin: 40px 0px 0px 0px;
    }

    .section-catalog__description {
        margin-top: 15px;
    }

    @media (max-width: 900px) {
        .section-catalog {
            padding-top: 150px;
        }
        .section-catalog__container {
            max-width: 100%;
            margin-left: auto;
            margin-right: auto;
            padding-left: 1.67vw;
            padding-right: 1.67vw;
        }
    }
    @media (max-width: 440px) {
        .section-catalog__container {
            margin: 9.09vw 0px 0px 0px;
        }
        .section-catalog {
            padding-top: 34.09vw;
        }
    }
</style>