       <div class="header__nav">
          <button @click="$router.push({name: 'main'})" class="header__nav-item header__nav-item-logo">UnHide</button>
          <button @click="toggleMenu" class="header__nav-item header__nav-item-menu">Menu</button>
          <button @click="toggleCart" class="header__nav-item header__nav-item-cart"><div class="header__nav-item-cart_icon"><span class="header__nav-item-cart_total">{{ cartProducts.length }}</span></div></button>
          <header-menu v-model:menuOpen="menuIsOpen"/>
          <header-cart-menu :cartOpen ="cartIsOpen"/>
       </div>
       <div class="header__nav-text">
          <div class="header__nav-text-block">
            <p class="header__nav-text-run">Free shipping on orders of $80 or more. <span class="header__nav-text-run_img"></span></p>
