</template>

<script>
import { mapState } from 'vuex';
import HeaderMenu from '@/components/HeaderMenu.vue';
import HeaderCartMenu from '@/components/HeaderCartMenu.vue';

export default {
    data() {
        return {
            menuIsOpen: false,
            cartIsOpen: false,
        }
    },
    components: { HeaderMenu, HeaderCartMenu},
    computed: {
        ...mapState({
            cartProducts: 'cartProducts',
        }),
    },
    methods: {
        toggleMenu() {
            this.cartIsOpen = false;
            this.menuIsOpen = !this.menuIsOpen;
        },
        toggleCart() {
            this.menuIsOpen = false;
            this.cartIsOpen = !this.cartIsOpen;
        }
    },
}
</script>

