<template>
      <div class="catalog-filter">
        <section class="section-filter">
          <ul v-show="isFilterOpen" :class="{'header__list-category_open': isFilterOpen}" class="header__list-category">
            <li class="header__list-category-item header__list-category-item_big"><button @click="newCategory(0)" class="header__list-category-btn">Shop All</button></li>
            <li v-for="category in categories" :key="category" class="header__list-category-item">
                <button @click="newCategory(category.id)" class="header__list-category-btn">{{ category.title }}</button>
            </li>
          </ul>
          <button @click="toggleFilter" :class="{'section-filter__open-filters_open': isFilterOpen}" class="section-filter__open-filters">Filters</button>
        </section>
        <div @click="isFilterOpen = false" :class="{'filter-mobile__wrapper': isFilterOpen}"></div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import categories from '@/data/categories';

export default {
    props: ['category'],
    emits: ['update:category'],
    data() {
        return {
            categories,
            isFilterOpen: false,
        }
    },
    methods: {
        toggleFilter() {
            this.isFilterOpen = !this.isFilterOpen;
        },
        ...mapMutations({
            changeCategory: 'changeCategory',
        }),
        newCategory(id) {
            this.changeCategory(id);
        }
    },
}
</script>

<style scoped>

    .catalog-filter {
        position: relative;
        max-width: 386px;
        width: 100%;
        right: 6.56vw;
        z-index: 1;
    }

    .section-filter {
        max-width: 386px;
        position: sticky;
        top: 176px;
        z-index: 2;
    }

    .header__list-category {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .header__list-category-item {
        width: 100%;
        max-width: 191px;
    }

    .header__list-category-item_big {
        margin-top: 3px;
        max-width: 100%;
    }

    .header__list-category-btn {
        max-width: 100%;
        width: 100%;
        padding: 22px 0px;
        border-radius: 4px;
        font-size: 18px;
        line-height: 24px;
        background-color: #fff;
        transition: background-color linear .2s;
    }

    .header__list-category-btn:hover {
        transition: background-color linear .2s;
        background-color: #EFECE7;
    }

    .header__list-category-item:not(:last-child) {
        margin-bottom: 3px;
    }

    .section-filter__open-filters {
        display: none;
    }

    @media (max-width: 900px) {
        .catalog-filter {
            width: 100%;
            max-width: 100%;
            position: fixed;
            left: 0;
            bottom: 0;
            right: 0;
        }

        .filter-mobile__wrapper {
            position: fixed;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: rgba(255,255,255,0.79);
            z-index: 1;
            transition: all linear .2s;
        }
        
        .section-filter {
            max-width: 100%;
            width: 100%;
        }

        .header__list-category-item {
            max-width: 48.78vw;
        }

        .header__list-category-item_big {
            max-width: 100%;
        }

        .header__list-category {
            opacity: 0;
            visibility: hidden;
            transition: all linear .2s;
        }
        
        .header__list-category_open {
            opacity: 1;
            visibility: visible;
            transition: all linear .2s;
        }

        .section-filter__open-filters {
            width: 100%;
            display: block;
            padding: 20px 0px;
            font-size: 18px;
            line-height: 21px;
            background-color: #fff;
            transition: all linear .2s;
        }

        .section-filter__open-filters_open {
            color: #fff;
            background-color: #161616;
            transition: all linear .2s;
        }

        .section-filter__open-filters::after {
            content: url('@/assets/img/svg/filters-open.svg');
            margin-left: 5px;
            vertical-align: 2px;
        }

        .section-filter__open-filters_open::after {
            content: url('@/assets/img/svg/filters-open-opened.svg');
        }
    }

    @media (max-width: 620px) {
        .header__list-category-item {
            max-width: 100%;
        }
    }

    @media (max-width: 440px) {
        .section-filter__open-filters {
            padding: 4.55vw 0px;
            font-size: 4.09vw;
            line-height: 4.77vw;
        }

        .header__list-category-btn {
            padding: 5vw 0px;
            border-radius: 0.91vw;
            font-size: 4.09vw;
            line-height: 5.45vw;
        }
    }
</style>