<template>
    <div class="checkout__form-adress-country checkout__form-wrapper">
        <label :for="name" class="label-helper label-active">{{ label }}</label>
        <select @change="changeValue" required class="checkout__form-adress-select input input-active" :id="name">
            <option v-for="option in options" :key="option.id" :value="option.id">{{ option.name }}</option>
        </select>
    </div>
</template>

<script>
export default {
    props: ['label', 'name', 'options', 'modelValue'],
    methods: {
        changeValue(e) {
            this.$emit('update:modelValue', e.target.value)
        }
    },
}
</script>

<style scoped>
    .checkout__form-adress-select {
        padding-left: 10px;
        padding-right: 10px;
    }
</style>