<template>
    <div class="checkout__form-contact-check">
        <label :label="id" class="product-info__form-label checkbox-label">
            <input v-model="checked" :id="id" class="checkout__form-checkbox checkbox" type="checkbox">
            <span class="checkout__form-checkbox-value checkbox-value"></span>
        </label>
        <small class="small-description">{{ text }} me with news and offers</small>
    </div>
</template>

<script>
export default {
    data() {
        return {
            checked: false,
        }
    },
    props: ['id', 'text', 'modelValue'],
    watch: {
        checked() {
            this.$emit('update:modelValue', this.checked);
        }
    }
}
</script>

<style scoped>
    .checkout__form-contact-check {
        display: flex;
        align-items: center;
        margin-top: 15px;
    }
    .product-info__form-label {
        margin-right: 15px;
    }

    @media (max-width: 440px) {
        .checkout__form-contact-check {
            margin-top: 3.41vw;
        }
        .product-info__form-label {
            margin-right: 3.41vw;
        }
    }
</style>